import Vue from 'vue'
import Vuex from 'vuex'
import { generateUuid, currentDatetime } from '../helpers/helper'
import router from '../router'

Vue.use(Vuex)

// set websocket

let token = process.env.VUE_APP_WEBSOCKET_TOKEN
let channel_id = process.env.VUE_APP_WEBSOCKET_CHANNEL_ID

const websocket = new WebSocket(`wss://connect.websocket.in/v2/${channel_id}?token=${token}`)

websocket.onmessage = function(e) {
  store.commit('addMessage', JSON.parse(e.data))
}

const store = new Vuex.Store({
  state: {
    is_loading: false,
    user: JSON.parse(localStorage.getItem('user') || "null"),
    messages: JSON.parse(localStorage.getItem('messages') || "[]")
  },
  mutations: {

    setUser(state, user) {
      state.user = user
      localStorage.setItem('user', JSON.stringify(user))
    },

    addMessage(state, message) {
      state.messages.push(message)
      localStorage.setItem('messages', JSON.stringify(state.messages))
    },

    reset(state) {
      state.user = null
      state.messages = []

      localStorage.removeItem('user')
      localStorage.removeItem('messages')
    },

    setLoading(state, loading) {
      state.is_loading = loading
    },

  },
  actions: {

    login({ commit }, name) {

      return new Promise((resolve, reject) => {

        let uuid =  generateUuid()

        let user = {
          uuid: uuid,
          name: name,
        }

        commit('setUser', user)

        resolve()

      })

    },

    logout({ commit, state }) {

      commit('reset')

      // redirect to login after logout
      router.push('/')

    },

    sendMessage({ commit, state }, message_text) {

      return new Promise((resolve, reject) => {

        commit('setLoading', true)

        let uuid =  generateUuid()

        let message = {
          uuid: uuid,
          user: state.user,
          text: message_text,
          created_at: currentDatetime()
        }

        websocket.send(JSON.stringify(message))

        commit('addMessage', message)

        commit('setLoading', false)

        resolve()


      })
    },

  },
  getters: {

    isLoggedIn(state) {

      if (state.user) {
        return true
      }

      return false
    },

    currentUser(state) {
      return state.user
    },

  },
  modules: {
  }
})

export default store
