<template>
  <fragment>
    <section class="intro">
      <h3 class="intro-header">Welcome to XOXZO Chat</h3>
      <h4 class="intro-message">To join the chat, enter your name and we are good to go!</h4>
    </section>
    <section class="message-box">
      <form @submit.prevent="handleSubmit">
        <input type="text" v-model="name" class="message-input" placeholder="Enter your name">
        <button type="submit" class="message-submit">SEND</button>
      </form>
    </section>
  </fragment>
</template>

<script>

export default {
  name: 'Login',
  components: {},
  data() {
    return {
      name: null
    }
  },
  methods: {

    // handle form submission
    async handleSubmit() {

      // check if empty
      if (!this.name) {
        console.log('Name is required')
        return
      }

      try {

        // perform login via vuex actions

        await this.$store.dispatch('login', this.name)

        // on success login, redirect to chatlist page

        this.$router.push('/chatlist')
      }
      catch (error) {
        console.log(error)
      }

    },

  }
}
</script>
